import React from "react";
import go from "programming-languages-logos/src/go-old/go-old.svg";
import js from "programming-languages-logos/src/javascript/javascript.svg";
import python from "programming-languages-logos/src/python/python.svg";

const GitListItem = (props) => {
  return (
    <div className="git-list-item" onClick={props.openModal.bind(null, props)}>
      <span className="git-list-item-name">{props.name}</span>
      <img
        className="git-list-item-img"
        src={
          props.languages === "Javascript"
            ? js
            : props.languages === "GO"
            ? go
            : python
        }
      ></img>
    </div>
  );
};

export default GitListItem;
