import React from "react";
import closeButton from "../resources/close.svg";
import gitButton from "../resources/GitHub-Mark-64px.png";

const GitModal = (props) => {
  return (
    <>
      {props.isOpen ? (
        <div className="modal" onClick={props.closeModal}>
          <div className="git-item-info-modal">
            <div className="close-button">
              <img src={closeButton} onClick={props.closeModal}></img>
            </div>
            <div className="git-item-info-data">
              <div className="data-field">
                프로젝트명:
                <span className="data-text"> {props.selectedItem.name} </span>
              </div>
              <div className="data-field">
                언어: <span>{props.selectedItem.languages}</span>
              </div>
              <div className="data-field">
                설명: <span>{props.selectedItem.description}</span>
              </div>
            </div>
            <div className="git-button">
              <a href={props.selectedItem.gitURL} target="_blank">
                <img src={gitButton}></img>
              </a>
            </div>
          </div>
        </div>
      ) : null}
    </>
  );
};

export default GitModal;
