import React from "react";

const EmailButton = () => {
  const sendMail = async () => {
    let email = document.querySelector(".your-email-address input").value;
    let title = document.querySelector(".contact-title input").value;
    let message = document.querySelector(".contact-message textarea").value;

    let mailObj = {
      email: email,
      title: title,
      message: message,
    };

    let response = await fetch("/mail", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(mailObj),
    });

    let result = await response.json();
    alert(result.message);
  };

  return (
    <div>
      <button className="button" onClick={sendMail}>
        전송
      </button>
    </div>
  );
};

export default EmailButton;
