import React, { useRef } from "react";
import "../style/nav.css";

const Nav = () => {
  const delay = useRef(-0.5);
  const btnEvent = () => {
    let btn = document.getElementById("btn");
    let subMenu = document.getElementById("sub-menu-list");
    if (btn.className === "on") {
      btn.classList.remove("on");
      for (let menu of subMenu.children) {
        menu.style.transitionDelay = delay.current + "s";
        delay.current -= 0.5;
        menu.classList.remove("active");
      }
    } else {
      btn.classList.add("on");
      for (let menu of subMenu.children) {
        delay.current += 0.5;
        menu.style.transitionDelay = delay.current + "s";
        menu.classList.add("active");
      }
    }
  };

  return (
    <header>
      <div className="wrapper">
        <h1>SEOL GANG</h1>
        <button id="btn" onClick={btnEvent}>
          <span></span>
          <span></span>
          <span></span>
        </button>
      </div>
      <div className="sub-menu">
        <ul id="sub-menu-list">
          <li>
            <a href="/">About Me</a>
          </li>
          <li>
            <a href="/mygit">My Git</a>
          </li>
          <li>
            <a href="/contact">Contact</a>
          </li>
        </ul>
      </div>
    </header>
  );
};

export default Nav;
